html,
body {
  overflow:hidden;
  width: 100%;
  height: 100%;
  /* max-width: 1366px; */
  /* max-height: 1024px; */
  margin: auto;
  border: 1px solid var(--primary);
}

.outline-container { 
  background-color: rgb(235, 233, 233); 
  max-width: 1080px;
  height: 790px;
  max-height: 790px;
  margin: auto;
  text-align: center;
  font-family: "Gill Sans", sans-serif;
  font-size: 22px;
}

.card { 
  height: 550px;
  max-width: 80%;
  margin: auto;
  margin-top: 10px;
  text-align: left;
}

.btn { 
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px; 
  width: 50px;
  height: 50px;
  font-size: 20px;
  text-decoration: none;
  color: white; 
  background-color:  rgb(35, 130, 255);
}

.btn:hover { 
  background-color: rgb(127, 181, 251);
}

a{
  color: white; 
  text-decoration: none;
}

a:hover{
  color: white; 
  text-decoration: none;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0,0,0,.5);
    box-shadow: 0 0 1px rgba(255,255,255,.5);
}

#root {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

.container,
.main {
  width: 100%;
  margin: 0 10px;
  height: calc(100% - 74px);
  /* overflow: hidden; */
}

.row {
  height: 100%;
  width: 100%;
  /* overflow: hidden; */
}

.col {
  height: 100%;
  padding: 15px;
  /* overflow-y: scroll; */
  /* -webkit-overflow-scrolling: touch; */
}

ul {
  list-style: none;
  padding-left: 20px;
  margin: 0px;
}
ul.first {
  padding-left: 5px;
}

.wrapper {
  width: 70vw;
  height: 85vh;
  margin: auto;
}

.dev {
  border: 1px dashed hotpink;
}

.empty-heading {
  padding-left: 13px;
}

.checkbox-icon {
  /* margin-top: 5px; */
  transform: translateY(5px);
}

.callout-icon {
  margin-left: 10px;
}

.callout {
  margin-left: 10px;
  /* color: blue; */
  font-style: italic;
  overflow: none;
  text-overflow: ellipsis;
}
.callout.currentCallOut {
  background-color: yellow;
}
.card-body {
  padding-top: 5px;
}

.intervention-form {
  margin-left: 20px;
}
.hidden {
  display: none;
}

th.table-header {
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: blue;
  color: white;
}

tbody.scrolling {
  overflow-y: auto;
}